.profile{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    gap: 1em;
}


/* Header */
.profile-header{
    text-align: left;
    color: var(--font-clr);
    font-weight: 300;
    margin: 0;
}
.profile span{
    color: var(--font-light);
}

/* Center section */
.profile-top{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 2em;
    padding: 1em;
    background: var(--main-clr);
    border-left: 1px solid var(--border-clr);
}
.profile-info-content{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
    gap: 1em;
}

/* Top nav */
.profile-top-info-top-nav{
    width: 100%;
    display: flex;
    align-items: baseline;
    gap: 1em;
}
.profile-top-info-top-nav-btn, .profile-top-info-top-nav-btn-active{
    width: max-content;
    position: relative;
    background: transparent;
    border: none;
    color: var(--font-light);
    padding: 0;
    cursor: pointer;
    transition: all 0.3s;
}
.profile-top-info-top-nav-btn::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: -8px;
    width: 100%;
    height: 2px;
    background: var(--secondary-clr);
    transform: scale(0);
}
.profile-top-info-top-nav-btn-active::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: -8px;
    width: 100%;
    height: 2px;
    background: var(--secondary-clr);
}


/* Pages */
.profile-pages-wrapper{
    width: 100%;
}