.profile-backlinks{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 1em;
}

.backlinks-title{
    font-size: 1.2rem;
    color: var(--font-clr);
    text-align: left;
    font-weight: 500;
    margin-bottom: 2em;
}

.backlinks-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.backlinks-wrapper-title{
    text-align: left;
    margin-bottom: 1em;
}
.backlinks-wrapper-sub{
    text-align: left;
    color: var(--font-light);
    margin-bottom: 1em;
}

.backlinks-wrapper-top{
    width: 100%;
    display: flex;
    border: 1px solid var(--border-clr);
    background: var(--main-clr);
    border-radius: var(--radius) var(--radius) 0 0;
    box-shadow: var(--material-shadow);
}
.backlinks-wrapper-top div{
    width: 15%;
    padding: 0.8em;
}
.backlinks-wrapper-top div:first-child {
    width: 30%;
}
.backlinks-wrapper-top div:nth-child(3){
    width: 30%;
}
.backlinks-wrapper-top div:nth-child(5){
    width: 10%;
}
.backlinks-wrapper-top div p{
    font-size: 0.9rem;
    color: var(--font-light);
    text-align: left;
    margin: 0;
}


.backlinks-empty{
    text-align: left;
    padding: 1em;
    color: var(--secondary-clr);
    background: var(--main-clr);
    border-top: none;
    box-shadow: var(--material-shadow);
}


/* Backlinks */
.backlinks{
    width: 100%;
    display: flex;
    flex-direction: column;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    /* border: 1px solid var(--border-clr); */
}
.backlink{
    background: var(--main-clr);
    width: 100%;
    height: max-content;
    display: flex;
    /* border-bottom: 1px solid var(--border-clr); */
    padding: 0.8em;
}
.backlink-post{
    width: 15%;
    height: 100%;
    overflow: hidden;
}
.backlink-post:first-child{
    width: 30%;
}
.backlink-post:nth-child(3){
    width: 30%;
}
.backlink-post:nth-child(5){
    width: 10%;
}
.backlink-post p{
    text-align: left;
    font-size: 0.9rem;
    color: var(--font-clr);
    margin: 0;
}
.backlink-post a{
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    color: var(--secondary-clr);
    white-space: normal;
    font-size: 0.9rem;
    text-align: left;
}


.backlink-actions-wrapper{
    width: 10%;
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
}
.backlink-actions-btn{
    background: transparent;
    border: none;
    cursor: pointer;
}
.backlink-actions-btns{
    position: absolute;
    bottom: -60px;
    display: flex;
    flex-direction: column;
    background: var(--secondary-clr);
    border-radius: 5px;
    overflow: hidden;
    z-index: 2;
}
.backlink-actions-btns button{
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: all 0.3s;
}
.backlink-actions-btns button:hover{
    background: var(--intense-clr);
}