.admin-panel-all-users-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    border: 1px solid var(--border-clr);
}

.admin-panel-all-users-empty{
    width: 100%;
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--secondary-clr);
    font-size: 0.9rem;
}

.admin-panel-all-users-categories {
    width: 100%;
    display: flex;
    align-items: center;
    background: rgb(238, 238, 238);
    pointer-events: none;
    border-bottom: var(--border-clr);
    background: var(--secondary-clr);
}
.admin-panel-all-users-categories p{
    width: 14%;
    color: white;
    font-size: 0.9rem;
    padding: 0.5em;
}


.admin-panel-all-users-single{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
}
.admin-panel-all-users-single-div{
    width: 12.5%;
    height: 50px;
    font-size: 0.9rem;
    color: var(--font-clr);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    padding: 1em;
    border-bottom: 1px solid var(--border-clr);
}
.admin-panel-all-users-single-name{
    text-transform: capitalize;
}
.admin-panel-all-users-single-div p{
    overflow-wrap: break-word;
    width: 80%;
    font-size: 0.8rem;
    margin: 0;
}
.admin-panel-all-users-active{
    color: green;
}
.admin-panel-all-users-inactive{
    color: red;
}


.admin-all-users-options-wrapper{
    position: absolute;
    right: 4em;
}
.admin-panel-all-users-options-btn{
    border: none;
    background: transparent;
    cursor: pointer;
    position: relative;
}
.admin-all-users-options{
    position: absolute;
    bottom: -85px;
    left: -40px;
    background: var(--secondary-clr);
    box-shadow: var(--material-shadow);
    border-radius: 5px;
    width: 100px;
    z-index: 3;
    display: flex;
    flex-direction: column;
}
.admin-all-users-options button{
    background: transparent;
    border: none;
    border-bottom: 1px solid var(--border-clr);
    color: white;
    padding: 0.3em 0.6em;
    cursor: pointer;
    transition: all 0.2s;
}
.admin-all-users-options button:hover{
    background: var(--intense-clr);
}