.admin-panel-websites{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.admin-panel-websites-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.admin-panel-websites-headings, .admin-panel-website{
    display: flex;
    align-items: center;
    width: 100%;
    border-bottom: 1px solid var(--border-clr);
}
.admin-panel-websites-headings{
    width: 100%;
    display: flex;
    align-items: center;
    background: rgb(238, 238, 238);
    pointer-events: none;
    border-bottom: var(--border-clr);
    border-radius: var(--radius) var(--radius) 0 0;
    background: var(--secondary-clr);
}
.admin-panel-websites-headings p{
    color: white;
    font-size: 0.9rem;
    padding: 0.5em;
    white-space: nowrap;
}
.admin-panel-websites-headings div, .admin-panel-website-data div{
    width: 15%;
    padding: 0.5em;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0.5em;
    font-size: 0.9rem;
    color: var(--font-clr);
}
.admin-panel-websites-headings div:nth-child(1){
    width: 25%;
}
.admin-panel-website-data div:nth-child(1){
    color: var(--secondary-clr);
    width: 25%;
}
.admin-panel-websites-empty{
    padding: 1em;
    border: 1px solid var(--border-clr);
    border-top: none;
}
.admin-panel-websites-empty p{
    color: var(--font-clr);
    font-size: 0.9rem;
    text-align: left;
}

/* Single website component */
.admin-panel-website{
    display: flex;
    flex-direction: column;
    border: 1px solid var(--border-clr);
    border-top: none;
    padding: 0.5em;
    transition: all 0.3s;
}
.admin-panel-website:hover{
    background: var(--main-light);
}
.admin-panel-website-data div:nth-child(1){
    width: 25%;
}
.admin-panel-website-data div p{
    font-size: 0.9rem;
    color: var(--font-clr);
}
.admin-panel-website-data div a{
    color: var(--secondary-clr);
    text-decoration: none;
}
.admin-panel-website-data{
    width: 100%;
    display: flex;
}


/* Functions */
.admin-panel-website-functions{
    width: 100%;
    background: var(--main-light);
    box-shadow: var(--material-shadow);
    border-top: 1px solid var(--border-clr);
    padding: 1em;
    display: flex;
    align-items: center;
    gap: 1em;
}
.admin-panel-website-functions p{
    color: var(--font-clr);
    font-size: 0.9rem;
}
.admin-panel-website-functions-dr-wrapper{
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.admin-panel-website-functions-dr-wrapper input{
    outline: none;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
}
.admin-panel-website-functions-dr-wrapper input::-webkit-outer-spin-button,
.admin-panel-website-functions-dr-wrapper input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.admin-panel-website-functions-dr-wrapper input[type=number] {
    -moz-appearance: textfield;
}
.admin-panel-website-functions-dr-wrapper input:focus{
    border-bottom: 1px solid var(--secondary-clr);
}



.admin-panel-website-functions-btns{
    display: flex;
    align-items: center;
    gap: 1em;
}
.admin-panel-websites-save-btn, .admin-panel-websites-spam-btn{
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: background 0.3s;
}
.admin-panel-websites-spam-btn{
    background: var(--warning-clr);
    color: var(--font-clr);
    border: 1px solid var(--border-clr);
}
.admin-panel-websites-save-btn:hover{
    background: var(--intense-clr);
}
.admin-panel-websites-spam-btn:hover{
    filter: brightness(90%);
}
