.login-wrapper{
    position: relative;
    width: 380px;
    height: 500px;
    background: #ffffff;
    border: 1px solid rgb(195, 195, 195);
    border-radius: var(--radius);
    display: flex;
    flex-direction: column;
    padding: 2em;
    gap: 1em;
}
.login-wrapper-p{
    width: 100%;
    text-align: center;
    font-size: 2rem;
    font-weight: 500;
    color: black;
    margin: 0;
}
.login-wrapper small{
    width: 100%;
    text-align: center;
    font-size: 0.8rem;
    color: var(--font-clr);
    margin: 0;
}


/* Login form */
#login-form{
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin: 2em 0;
}

.login-input-wrapper{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 0.5em;
    border-bottom: 1px solid grey;
    padding: 0.2em 0.4em;
}
.login-input-wrapper:focus-within{
    border-bottom: 2px solid var(--secondary-clr);
}

.login-text-input, .login-password-input{
    width: 90%;
    background: transparent;
    border: none;
    padding: 1em 2em;
    outline: none;
}
#login-form label{
    font-size: 0.8rem;
}
.login-icons{
    width: 10%;
    color: grey;
}
.form-btn{
    color: var(--main-clr);
    background: var(--secondary-clr);
    border: none;
    border-radius: var(--radius);
    padding: 1em 2em;
    cursor: pointer;
    transition: all 0.3s;
    will-change: background;
}
.form-btn:hover{
    background: var(--intense-clr);
}
.keep-signed-btn{
    background: transparent;
    border: none;
    width: max-content;
}
.login-checkbox{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 0.5em;
}

.show-password-btn{
    position: absolute;
    right: 0.6em;
    background: transparent;
    color: var(--font-clr);
    font-size: 1rem;
    border: none;
    cursor: pointer;
}


/* Forgot pass */
.forgot-pass-btn{
    text-decoration: none;
    font-size: 0.9rem;
    background: transparent;
    border: none;
    color: var(--secondary-clr);
    cursor: pointer;
    transition: all 0.3s;
    will-change: color;
}
.forgot-pass-btn:hover{
    color: var(--intense-clr);
}

/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .login-wrapper{
        display: none;
    }
}
@media only screen and (max-width: 480px){
    .login-wrapper{
        display: none;
    }
}