.profile-info{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 3em;
}

.profile-info-details{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    box-shadow: var(--material-shadow);
    border-radius: var(--radius);
    padding: 1em;
}
.profile-info-details-title{
    text-align: left;
    margin: 0;
}
.profile-info-details div{
    display: flex;
    align-items: center;
    gap: 1em;
    color: var(--font-clr);
}
.profile-info-details div span{
color: var(--font-light);
}
.profile-info-connections{
    color: var(--secondary-clr);
}


.profile-settings{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.profile-settings-title{
    text-align: left;
    margin: 0;
}
.profile-settings small{
    color: var(--font-light);
    text-align: left;
}
.profile-settings-btns{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 1em;
}
.profile-settings-btns button{
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
}