:root{
  --main-clr: #ffffff;
  --main-light: #e2e4e7;
  --secondary-clr: #003dff;
  --secondary-light: #489EB7;
  --third-clr: #FFC200;
  --third-clr-intense: #ffaa00;
  --intense-clr: #6148B7;
  --font-clr: #2c2c2c;
  --font-light: #6b6b6b;
  --border-clr: #c2c2c2;
  --success-clr: #49ff46;
  --fail-clr: #ff3333;
  --warning-clr: rgb(255, 255, 84);
  --radius: 10px;
  --material-shadow: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
}

*{
  box-sizing: border-box;
}

html{
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
body{
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}
body p{
  margin: 0;
}
body textarea{
  font-family: 'Roboto', sans-serif;
}


.app-wrapper {
  text-align: center;
}