/* Pages websites */
.profile-websites{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding-bottom: 2em;
}

.profile-websites-title, .profile-websites-add-title, .profile-websites-active-title{
    margin: 0;
    color: var(--font-clr);
    font-size: 1rem;
    text-align: left;
}
.profile-websites-add-btn, .profile-websites-active-btn{
    width: max-content;
    background: var(--secondary-clr);
    border: 1px solid var(--secondary-clr);
    border-radius: 5px;
    color: var(--main-clr);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    padding: 0.5em 1em;
}
.profile-websites-add-btn:hover, .profile-websites-active-btn:hover{
    background: var(--main-clr);
    color: var(--secondary-clr);
}


/* Website component and single websites */
.profile-websites-div{
    display: flex;
    flex-direction: column;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border-radius: var(--radius);
    overflow: hidden;
    margin-bottom: 2em;
}
.profile-websites-div-titles, .profile-website{
    width: 100%;
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--border-clr);
    box-shadow: var(--material-shadow);
}
.profile-websites-div-titles{
    background: var(--main-clr);
    color: var(--font-light);
}
.profile-websites-div-titles-div, .profile-website-div{
    width: 14%;
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    overflow: hidden;
    gap: 0.5em;
}
.profile-website-div{
    color: var(--font-clr);
}
.profile-website-div:nth-child(1){
    color: var(--secondary-clr);
}
.profile-website-div-status{
    position: relative;
    cursor: help;
}
.profile-website-div-status:hover:after{
    display: block;
}
.profile-website-div-status::after{
    position: absolute;
    right: -110px;
    width: 90px;
    top: -10px;
    content: "Restricted due to it's content";
    background: var(--main-clr);
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    box-shadow: var(--material-shadow);
    padding: 0.2em 0.4em;
    font-size: 0.7em;
}
.profile-website-div-status-approved::after{
    content: "Website was validated";
    display: none;
}
.profile-website-div-status-pending::after{
    content: "Waiting for admin confirmation";
    display: none;
}
.profile-website-div-status-spam::after{
    content: "Restricted due to it's content";
    display: none;
}
.profile-websites-div-titles-div p, .profile-website-div p{
    text-align: left;
    margin: 0;
    font-weight: 500;
    font-size: 0.9rem;
    white-space: nowrap;
}
.profile-website-div p{
    color: var(--font-clr);
    font-weight: normal;
}
.profile-website-div a{
    width: 100%;
    text-decoration: none;
    color: var(--secondary-clr);
    font-size: 0.9rem;
    display: flex;
    justify-content: flex-start;
}
.profile-websites-div-titles-actions{
    position: absolute;
    right: 5em;
}


/* Websites add*/
.profile-websites-add, .profile-websites-active{
    display: flex;
    flex-direction: column;
    gap: 1em;
    margin-bottom: 2em;
}
.profile-websites-add-sub, .profile-websites-active-sub{
    text-align: left;
    color: var(--font-light);
}
.profile-website-form-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(255, 255, 255, 0.77);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 30;
}
#profile-website-form{
    position: relative;
    background: white;
    width: 35%;
    box-shadow: var(--material-shadow);
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    padding: 3em 2em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.profile-website-form-title{
    text-align: left;
    margin: 0;
    color: var(--font-clr);
    font-weight: 500;
    font-size: 1.2rem;
}
.profile-website-form-sub{
    text-align: left;
    color: var(--font-light);
    font-size: 0.9rem;
    margin-bottom: 1em;
}
.profile-website-form-close-btn{
    position: absolute;
    right: 15px;
    top: 15px;
    width: 2em;
    height: 2em;
    background: transparent;
    border: 1px solid var(--secondary-clr);
    color: var(--secondary-clr);
    border-radius: 50%;
    cursor: pointer;
    transition: background 0.3s color 0.3s;
}
.profile-website-form-close-btn:hover{
    background: var(--secondary-clr);
    color: white;
}
.profile-website-form-first, .profile-website-form-second{
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.profile-website-form-next-btn{
    width: 100%;
    background: var(--secondary-clr);
    border: none;
    border-radius: var(--radius);
    color: white;
    padding: 1em 2em;
    cursor: pointer;
    transition: background 0.3s;
    will-change: background;
}
.profile-website-form-next-btn:hover{
    background: var(--intense-clr);
}



.profile-websites-confirmation{
    position: fixed;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 2em 4em;
    background: white;
    border-radius: var(--radius);
    border: 1px solid var(--border-clr);
    box-shadow: var(--material-shadow);
}
.profile-websites-confirmation div{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 0.2em;
}
.profile-websites-confirmation div p{
    font-size: 0.9rem;
    color: var(--font-clr);
}
.profile-websites-confirmation div small{
    font-size: 0.8rem;
    color: var(--font-light);
}