.connections-check{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5em;
    padding: 3em;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    overflow: hidden;
}
.connections-check img{
    position: absolute;
    z-index: -1;
    width: 120%;
    transform: translateX(0%) translateY(0%);
    opacity: 0;
}
.connections-check-close-btn{
    position: absolute;
    top: 1em;
    right: 1em;
    background: var(--main-clr);
    border: 1px solid var(--secondary-clr);
    color: var(--secondary-clr);
    border-radius: 5px;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: all 0.3s;
}
.connections-check-close-btn:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}
.connections-check h1{
    font-size: 1.1rem;
    font-weight: 400;
    color: var(--font-clr);
    margin: 0;
}
.connections-check p{
    color: var(--font-light);
    width: 60%;
    line-height: 1.5em;
    margin: 0;
}
.connections-check-name{
    color: var(--secondary-clr);
    text-transform: capitalize;
    font-weight: 500;
}
.connection-check-btns{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
}
.connection-check-btns button{
    padding: 1em 2em;
    border-radius: 5px;
    border: none;
    background: var(--secondary-clr);
    color: var(--main-clr);
    cursor: pointer;
}
.connection-check-btns button:nth-child(2){
    background: var(--third-clr);
    color: var(--secondary-clr);
}