.posts{
    width: 100%;
    height: 100vh;
    padding: 1em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.posts-title{
    color: var(-font-clr);
    font-weight: 500;
    font-size: 1.2rem;
    text-align: left;
    margin-bottom: 2em;
}
.posts-sub{
    text-align: left;
    color: var(--font-light);
    font-size: 0.9rem;
    line-height: 1.5em;
}


/* Posts options bar */
.posts-options-bar{
    background: var(--main-clr);
    width: 100%;
    box-shadow: var(--material-shadow);
    border-radius: 5px;
    padding: 0.5em 1em;
}
.posts-options-search{
    width: max-content;
    display: flex;
    align-items: center;
    gap: 0.5em;
    box-shadow: var(--material-shadow);
    border-radius: 5px;
    padding: 0.5em 1em;
}
.posts-options-search input{
    border: none;
    outline: none;
    background: transparent;
}


.posts-wrapper{
    width: 100%;
}
.posts-wrapper-header{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 1em;
    margin-bottom: 1em;
}
.posts-wrapper-add-btn{
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: all 0.3s;
}
.posts-wrapper-add-btn:hover{
    background: var(--intense-clr);
}


.posts-wrapper-top{
    width: 100%;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border: 1px solid var(--border-clr);
    display: flex;
    align-items: center;
    color: var(--font-light);
    border-radius: var(--radius) var(--radius) 0 0;
}
.posts-wrapper-top-div{
    width: 20%;
    padding: 0.8em;
}
.posts-wrapper-top-div p, .posts-wrapper-top-actions p{
    font-size: 0.9rem;
    text-align: left;
}
.posts-wrapper-empty{
    background: var(--main-clr);
    border: 1px solid var(--border-clr);
    padding: 0.5em;
}
.posts-wrapper-empty p{
    text-align: left;
    margin: 1em;
    color: var(--secondary-clr);
}


/* Profile posts single post */
.post-single{
    position: relative;
    width: 100%;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border-bottom: 1px solid var(--border-clr);
    display: flex;
    align-items: center;
}
.post-single-div{
    width: 20%;
    height: 100%;
    padding: 0.8em;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    gap: 0.5em;
}
.post-single-div p{
    color: var(--font-clr);
    text-align: left;
    font-size: 14px;
    white-space: nowrap;
}
.post-single-div p:nth-child(2), .post-single-div p:nth-child(3){
    color: var(--third-clr);

}
.post-single-div a, .post-single-icon{
    color: var(--secondary-clr);
    text-decoration: none;
    font-size: 0.9rem;
    text-align: left;
}
.post-single-actions{
    position: relative;
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
}
.post-single-actions-expand-btn{
    position: relative;
    background: transparent;
    border: none;
    cursor: pointer;
    z-index: 1;
}
.post-single-actions-btns{
    position: absolute;
    bottom: -65px;
    left: -40px;
    background: var(--secondary-clr);
    box-shadow: var(--material-shadow);
    border-radius: 5px;
    width: 100px;
    display: flex;
    flex-direction: column;
    z-index: 2;
}
.post-single-actions-btns button{
    color: var(--main-clr);
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0.5em 1em;
    border-radius: 5px;
}
.post-single-actions-btns button:hover{
    background: var(--intense-clr);
}


/* Post links form */
.post-links-form-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(255, 255, 255, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1em;
}
.post-links-form-wrapper p{
    color: var(--font-clr);
    font-weight: 500;
    text-align: left;
}
#post-links-form{
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 30%;
    background: var(--main-clr);
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    box-shadow: var(--material-shadow);
    padding: 3em 2em;
}
#post-links-form input{
    width: 100%;
    height: 3em;
    background: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid var(--secondary-clr);
    padding: 0.5em 1em;
}
#post-links-form input:focus{
    border-bottom: 2px solid var(--secondary-clr);
}
.post-links-form-submit-btn{
    width: 100%;
    height: 3em;
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-radius: var(--radius);
    cursor: pointer;
    transition: background 0.3s;
}
.post-links-form-submit-btn:hover{
    background: var(--intense-clr);
}
.post-links-form-close-btn{
    position: absolute;
    right: 1em;
    top: 1em;
    width: 2.5em;
    height: 2.5em;
    border: 1px solid var(--border-clr);
    border-radius: 50%;
    cursor: pointer;
}
.post-links-form-close-btn:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}