/* Profile sidebar */
.profile-sidebar{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 15%;
    height: 90vh;
    background: var(--main-clr);
    border-right: 1px solid var(--border-clr);
}


.profile-sidebar-top{
    position: relative;
    padding: 1em;
    width: 100%;
    height: 25%;
    border-bottom: 1px solid var(--border-clr);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}
.profile-sidebar-top-profile{
    width: 3em;
    height: 3em;
    border-radius: 50%;
    border: 1px solid var(--border-clr);
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-sidebar-top h5{
    margin: 0;
    font-size: 1rem;
    color: var(--main-clr);
}
.profile-sidebar-top p{
    margin: 0;
    color: var(--main-clr);
    font-size: 0.8rem;
    pointer-events: none;
}
.profile-sidabar-close-btn{
    background: white;
    border: none;
    color: var(--secondary-clr);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -1px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3em;
    font-size: 1.2rem;
    cursor: pointer;
    z-index: 25;
}



.profile-sidebar-options{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.profile-sidebar-options-top, .profile-sidebar-options-bottom{
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.profile-sidebar-options-top p{
    text-align: left;
    margin: 1em 0 0 1em;
    font-size: 0.9rem;
    color: var(--font-clr);
}

.profile-sidebar-options-btn{
    text-decoration: none;
    position: relative;
    background: transparent;
    border: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 0.4em 0.4em 0.4em 1em;
    color: var(--font-clr);
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s;
}
.profile-sidebar-options-btn:hover{
    background: rgb(95, 95, 255);
    color: white;
}
.profile-sidebar-options-btn-active{
    text-decoration: none;
    position: relative;
    background: linear-gradient(to left, rgba(0, 17, 255, 0), #ba8d107e);
    border: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 0.4em 0.4em 0.4em 1em;
    color: var(--font-clr);
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s;
}
.profile-sidebar-options-btn-span{
    width: 4px;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: var(--third-clr);
}

.profile-sidebar-options-count{
    position: absolute;
    right: 0.8em;
    color: var(--secondary-clr);
    font-size: 0.7rem;
    background: var(--third-clr);
    border-radius: 50%;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    justify-content: center;
    align-items: center;
}
