.error-message {
    width: 100%;
}
.error-p{
    color: rgb(255, 45, 45);
    margin: 0;
    font-size: 0.8rem;
    text-align: left;
}


.error-popup{
    width: 200px;
    position: fixed;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
    background: var(--fail-clr);
    color: white;
    box-shadow: var(--material-shadow);
    border-radius: var(--radius);
    padding: 1em;
}
.error-popup p{
    text-align: left;
    font-size: 0.8rem;
}


.success-message{
    background: var(--main-clr);
    position: fixed;
    left: 50%;
    bottom: 5%;
    transform: translateX(-50%);
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    box-shadow: var(--material-shadow);
    padding: 2em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}
.success-message p{
    margin: 0;
    color: var(--font-clr);
}
.success-message small{
    margin: 0;
    color: var(--font-light);
}


.token-expired-message{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.745);
    color: var(--font-clr);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 30;
}
.token-expired-message div{
    position: relative;
    width: 30%;
    padding: 2em;
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    box-shadow: var(--material-shadow);
    background: var(--main-clr);
}
.token-expired-message button{
    position: absolute;
    width: 2em;
    height: 2em;
    right: 0.5em;
    top: 0.5em;
    border: 1px solid var(--border-clr);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s;
}
.token-expired-message button:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
}