.admin-panel-users-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.admin-panel-users-empty{
    width: 100%;
    padding: 1em;
    font-size: 0.9rem;
    color: var(--secondary-clr);
}


.admin-panel-users-categories{
    width: 100%;
    display: flex;
    align-items: center;
    background: rgb(238, 238, 238);
    pointer-events: none;
    border-bottom: var(--border-clr);
    border-radius: var(--radius) var(--radius) 0 0;
    background: var(--secondary-clr);
}
.admin-panel-users-categories p{
    width: 25%;
    color: white;
    font-size: 0.9rem;
    padding: 0.5em;
}


.admin-panel-user{
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.admin-panel-user-data{
    width: 100%;
    display: flex;
    align-items: center;
}
.admin-panel-user-data div{
    width: 20%;
    height: 50px;
    font-size: 0.9rem;
    color: var(--font-clr);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    padding: 1em;
    border-bottom: 1px solid var(--border-clr);
    overflow: hidden;
}
.admin-panel-user-data div p{
    overflow-wrap: break-word;
    width: 80%;
    font-size: 0.8rem;
    margin: 0;
}
.admin-panel-user-data div a{
    color: var(--secondary-clr);
    text-decoration: none;
    cursor: pointer;
    font-size: 0.8rem;
}
.admin-panel-user-name{
    text-transform: capitalize;
}
.admin-panel-user-icons{
    width: 20%;
}
.admin-show-details-btn{
    background: transparent;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
}
.admin-show-details-btn-active{
    background: transparent;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
    transform: rotate(180deg);
}


/* Admin panel functions */
.admin-panel-user-functions{
    width: 100%;
    border: 1px solid var(--border-clr);
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1em;
    background: var(--main-light);
    box-shadow:  var(--material-shadow);
}
.admin-panel-user-functions-btns{
    display: flex;
    align-items: center;
    gap: 1em;
    padding-right: 2em;
}
.admin-panel-reject-btn, .admin-panel-message-btn, .admin-panel-activate-btn{
    color: var(--font-clr);
    border: 1px solid var(--border-clr);
    border-radius: 5px;
    padding: 0.5em 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    cursor: pointer;
    transition: background 0.2s;
}
.admin-panel-reject-btn:hover, .admin-panel-message-btn:hover{
    background: #cacaca;
}
.admin-panel-activate-btn{
    border: none;
    background: var(--secondary-clr);
    color: white;
    border-radius: 5px;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: all 0.3s;
}
.admin-panel-activate-btn:hover{
    background: var(--intense-clr);
    color: white;
}

.website-rating{
    width: 51%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0.5em;
}
.website-rating p, span{
    font-size: 0.9rem;
    color: var(--secondary-clr);
}

.website-rating-slider {
  -webkit-appearance: none;
  background: var(--secondary-light);
  border-radius: var(--radius);
  width: 70%;
  height: 10px;
  outline: none;
  -webkit-transition: .2s;
  transition: opacity .2s;
}
.website-rating-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 35px;
  height: 15px;
  background: var(--third-clr);
  cursor: pointer;
  border-radius: var(--radius);
}
.website-rating-slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #04AA6D;
  cursor: pointer;
}



/* Success modal */
.admin-success-modal{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 1em 2em;
    background: white;
    border-radius: var(--radius);
    border: 1px solid var(--font-clr);
}

.admin-success-modal div{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 0.2em;
}
.admin-success-modal div p{
    font-size: 0.9rem;
    color: var(--font-clr);
}
.admin-success-modal div small{
    font-size: 0.8rem;
    color: var(--font-light);
}


/* Delete user confirmation */
.delete-user-confirmation{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    border: 1px solid var(--border-clr);
    border-radius: var(--radius);
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    padding: 1em;
}
.delete-user-confirmation p{
    color: var(--font-clr);
    margin: 0;
}
.delete-user-confirmation small{
    color: var(--font-light);
    width: 70%;
    margin: 0;
}
.delete-user-confirmation div{
    display: flex;
    align-items: center;
    gap: 1em;
}
.delete-user-btn, .cancel-delete-user-btn{
    background: var(--border-clr);
    border: 1px solid var(--border-clr);
    border-radius: 5px;
    color: var(--font-clr);
    padding:  1em 2em;
    cursor: pointer;
    transition: all 0.3s;
}
.delete-user-btn:hover{
    background: white;
}
.cancel-delete-user-btn{
    background: var(--secondary-clr);
    color: white;
}
.cancel-delete-user-btn:hover{
    background: var(--intense-clr);
}