.profile-conversations{
    width: 100%;
    height: 100%;
    display: flex;
}

/* Conversation chats */
.conversation-messages{
    position: relative;
    width: 70%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    border-right: 1px solid var(--border-clr);
    border-top: 1px solid var(--border-clr);
    padding: 1em;
}
.conversation-messages-empty{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.conversation-messages-empty h1{
    font-weight: 300;
    color: var(--font-clr);
}
.conversation-messages-empty p{
    color: var(--font-clr);
}

/* Conversation chat single */
.conversation-messages-single{
    width: 100%;
    height: 100%;
}
.conversation-messages-single-text{
    width: 100%;
    height: 85%;
}
.conversation-messages-single-text-title{
    height: 10%;
    font-weight: 300;
    color: var(--font-clr);
    font-size: 1rem;
    margin: 0;
    background: transparent;
}
.conversation-messages-single-text p{
    color: var(--font-clr);
}
.conversation-messages-single span{
    text-transform: capitalize;
    color: var(--secondary-clr);
}
.conversation-messages-single-messages{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    padding-bottom: 1em;
    overflow-y: scroll;
    overscroll-behavior-y: contain;
    scroll-snap-type: y proximity;
}
.conversation-messages-single-messages::-webkit-scrollbar {
  width: 0px;
}

/* Single messages */
.conversation-messages-single-messages-wrapper{
    display: flex;
    align-items: center;
    gap: 1em;
    margin-left: 1em;
}
.conversation-messages-single-messages-wrapper-options{
    position: absolute;
    top: -45px;
    display: flex;
    align-items: center;
    gap: 0.5em;
    border-radius: 25px;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    padding: 0.3em;
    z-index: 40;
}
.conversation-messages-single-messages-wrapper-options button{
    width: 1.5em;
    height: 1.5em;
    border: none;
    border-radius: 50%;
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.conversation-messages-single-messages-wrapper-options button:nth-child(2):hover{
    background: var(--warning-clr);
}
.conversation-messages-single-messages-wrapper-options button:nth-child(3):hover{
    background: var(--fail-clr);
}
.conversation-messages-single-messages-wrapper-options button:nth-child(1):hover{
    background: var(--intense-clr);
    color: var(--main-clr);
}
.conversation-messages-single-messages-img{
    width: 25px;
    height: 25px;
    background: var(--third-clr);
    color: var(--main-clr);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.conversation-messages-single-messages-sender-wrapper, .conversation-messages-single-messages-reciever-wrapper{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 0.5em;
}
.conversation-messages-single-messages-sender-wrapper p {
    color: var(--main-clr);
    text-align: right;
    font-size: 0.9rem;
    user-select: none;
}
.conversation-messages-single-messages-reciever-wrapper p{
    color: var(--font-clr);
    text-align: left;
    font-size: 0.9rem;
    user-select: none;
}
.conversation-messages-single-messages-reciever-wrapper{
    align-items: flex-start;
    gap: 0.5em;
}
.conversation-messages-single-messages-sender, .conversation-messages-single-messages-reciever{
    max-width: 50%;
    background: var(--secondary-clr);
    padding: 0.5em 1em;
    border-radius: var(--radius) var(--radius) 0 var(--radius);
    box-shadow: var(--material-shadow);
    cursor: pointer;
}
.conversation-messages-single-messages-reciever{
    background: var(--main-clr);
    border-radius: var(--radius) var(--radius) var(--radius) 0;
    box-shadow: var(--material-shadow);
}
.conversation-messages-single-messages-time{
    font-size: 0.8rem;
    color: var(--font-light);
}

/* Conversation form */
#conversation-messages-single-form{
    width: 100%;
    height: 15%;
    padding: 0.5em 1em;
    background: var(--main-clr);
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: var(--material-shadow);
    border-radius: 0 0 var(--radius) var(--radius);
    border-top: 1px solid var(--border-clr);
}
#conversation-messages-single-form input{
    border: none;
    border-radius: var(--radius);
    background: var(--main-light);
    outline: none;
    padding: 1em;
    width: 80%;
}
.profile-conversation-attach-btn, .profile-conversation-send-btn{
    background: transparent;
    font-size: 1.2rem;
    border: none;
    cursor: pointer;
    color: var(--font-clr);
}
.profile-conversation-send-btn{
    color: var(--main-clr);
    background: var(--third-clr);
    padding: 0.5em 1em;
    border-radius: var(--radius);
    transition: all 0.3s;
}
.profile-conversation-send-btn:hover{
    transform: scale(1.1);
    backdrop-filter: brightness(50%);
}


/* Conversations sidebar */
.conversation-sidebar{
    width: 30%;
    height: 100%;
    border-top: 1px solid var(--border-clr);
    background: var(--main-clr);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5em;
    padding-top: 2em;
}
.conversation-sidebar h1{
    font-weight: 300;
    color: var(--font-clr);
    font-size: 1.5rem;
    margin: 0;
    margin-bottom: 1em;
}

.conversation-sidebar-search{
    width: 90%;
    padding: 0.5em;
    background: var(--main-light);
    border-radius: var(--radius);
    display: flex;
    align-items: center;
    gap: 0.5em;
    margin-bottom: 1em;
}
.conversation-sidebar-search input{
    width: 90%;
    border: none;
    outline: none;
    background: transparent;
}

.conversation-sidebar-header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.5em;
}
.conversation-sidebar-label{
    width: 100%;
    text-align: left;
    padding: 0 1em;
    font-weight: 500;
    color: var(--font-light);
}
.conversation-sidebar-header-btns{
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.conversation-sidebar-header-btns button:nth-child(1){
    background: var(--main-clr);
    border: none;
    border-radius: var(--radius);
    box-shadow: var(--material-shadow);
    padding: 0.3em 0.6em;
    cursor: pointer;
}
.conversation-sidebar-header-btns button:nth-child(2){
    background: transparent;
    border: none;
    cursor: pointer;
}


/* Conversations sidebar single */
.conversation-sidebar-single, .conversation-sidebar-single-active{
    position: relative;
    width: 90%;
    display: flex;
    align-items: center;
    gap: 1em;
    background: var(--main-clr);
    /* border: 1px solid var(--border-clr); */
    border-radius: var(--radius);
    box-shadow: var(--material-shadow);
    padding: 1em;
    cursor: pointer;
}
.conversation-sidebar-single-active{
    background: var(--main-light);
}
.conversation-sidebar-single:hover{
    background: var(--main-light);
}
.conversation-sidebar-single-img{
    width: 30px;
    height: 30px;
    background: var(--secondary-clr);
    color: var(--main-clr);
    border-radius: 50%;
    border: 2px solid var(--secondary-clr);
    display: flex;
    justify-content: center;
    align-items: center;
}

.conversation-sidebar-single-title{
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}
.conversation-sidebar-single-title-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.conversation-sidebar-single-name{
    color: var(--font-clr);
    font-size: 0.9rem;
    text-transform: capitalize;
}
.conversation-sidebar-single-title-div small{
    font-size: 0.8rem;
    color: var(--secondary-clr);
    pointer-events: none;
}
.conversation-sidebar-single-message{
    text-align: left;
    color: var(--font-light);
    font-size: 0.8rem;
}
.conversation-sidebar-single-message-unopened{
    text-align: left;
    color: black;
    font-weight: 500;
    font-size: 0.8rem;
}
.conversation-sidebar-single-message-new{
    position: absolute;
    width: 17px;
    height: 17px;
    right: 1.8em;
    bottom: 1.5em;
    background: var(--intense-clr);
    border-radius: 50%;
    color: var(--main-clr);
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
}