.landing-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Header */
.header-wrapper{
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    gap: 2em;
    padding: 7em 7em 0 5em;
}


.login-img{
    position: absolute;
    width: 75%;
    height: 75%;
    right: 5%;
    bottom: -25%;
    z-index: 2;
    pointer-events: none;
}
.header-shape-img{
    position: absolute;
    right: -55%;
    top: -90%;
    transform: scale(0.5);
    z-index: -1;
    pointer-events: none;
}


.header-text{
    width: 40%;
    display: flex;
    flex-direction: column;
    gap: 2em;
}
.header-text h1{
    text-align: left;
    font-size: 3.5rem;
    font-weight: 400;
    color: var(--font-clr);
    line-height: 0.9em;
    margin: 0;
}
.header-text p{
    text-align: left;
    color: var(--font-light);
    line-height: 1.5em;
    font-size: 1.1rem;
    margin: 0;
}
.sign-up-btn{
    text-decoration: none;
    width: max-content;
    display: flex;
    background: var(--third-clr);
    color: white;
    border-radius: var(--radius);
    padding: 1em 2em;
}

.header-fallback{
    width: 100%;
    height: 5vh;
    background: var(--secondary-light);
}


/* Info  */
.landing-info-wrapper{
    width: 100%;
    min-height: 100vh;
    margin: 15em 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    gap: 2em;
}
.landing-info{
    background: var(--main-clr);
    box-shadow:  6px 6px 12px #e8e8e8,
    -6px -6px 12px #e8e8e8;
    width: 25%;
    min-height: 350px;
    border-radius: var(--radius);
    padding: 2em;
    margin-bottom: 2em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2em;
    transition: all 0.3s;
}
.landing-info:hover{
    transform: scale(1.1);
    background: rgb(238, 238, 238);
}
.landing-info-icon{
    font-size: 3rem;
    color: var(--secondary-light);
}
.landing-info h1{
    font-size: 2rem;
    width: 60%;
    font-weight: 400;
    text-align: left;
    color: var(--font-clr);
    margin: 0;
}
.landing-info p{
    text-align: left;
    color: var(--font-light);
    margin: 0;
    line-height: 1.5em;
}


/* Section-1 */
.section-1{
    position: relative;
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2em;
    margin: 5em 0;
}
.section-1 img{
    position: absolute;
    top: -20%;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
}
.section-1 h1{
    margin: 0;
    font-size: 3rem;
    font-weight: 600;
    color: white;
}
.section-1 p{
    margin: 0;
    font-size: 2.5rem;
    color: white;
}
.section-1 button{
    background: orange;
    border: none;
    border-radius: var(--radius);
    padding: 1em 2em;
    color: white;
    font-size: 1.2rem;
}


/* Section-2 */
.section-two{
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
}
.section-two-wrappers{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.section-two-wrappers img{
    width: 50%;
    height: 50%;
}
.section-two-content{
    width: 40%;
    padding: 5em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.section-two-content h1{
    text-align: left;
    color: var(--font-clr);
    margin: 0;
}
.section-two-content p{
    text-align: left;
    color: var(--font-clr);
    margin: 0;
    line-height: 1.5em;
}
.section-two-links{
    text-decoration: none;
    font-weight: 500;
    font-size: 1.1rem;
    color: var(--secondary-clr);
    width: max-content;
    display: flex;
    align-items: center;
    gap: 0.3em;
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .login-img{
        display: none;
    }
    .header-wrapper{
        flex-direction: column-reverse;
        align-items: center;
        padding: 5em 0;
    }
    .header-text{
        width: 90%;
    }


    .landing-info{
        width: 90%;
    }


    .section-1 p{
        text-align: center;
        font-size: 1.5rem;
    }
    .section-1 img{
        top: -15%;
    }


    .section-two-wrappers{
        flex-direction: column;
    }
    .section-two-wrappers:nth-child(2){
        flex-direction: column-reverse;
    }
    .section-two-content{
        width: 100%;
        padding: 1em;
    }
    .section-two-wrappers img{
        width: 70%;
        height: 70%;
    }
}

@media only screen and (max-width: 480px){
    .header-wrapper{
        flex-direction: column-reverse;
        align-items: center;
        padding: 5em 0;
    }
    .header-text{
        width: 90%;
    }
    /* .header-shape-img{
        top: 50%;
        right: 0;
        transform: scale(2);
    } */
}