.profile-outlinks{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 1em;
}

.outlinks-title{
    font-size: 1.2rem;
    color: var(--font-clr);
    text-align: left;
    font-weight: 500;
    margin-bottom: 2em;
}

.outlinks-wrapper{
    width: 100%;
    padding-bottom: 5em;
}
.outlinks-wrapper-empty{
    padding: 1em;
    background: var(--main-clr);
    border: 1px solid var(--border-clr);
}
.outlinks-wrapper-empty p{
    text-align: left;
    color: var(--font-light);
}
.outlinks-wrapper-p{
    text-align: left;
    margin-bottom: 1em;
}
.outlinks-wrapper-sub{
    text-align: left;
    color: var(--font-light);
    margin-bottom: 1em;
    font-size: 0.9rem;
    line-height: 1.5em;
}
.outlinks-wrapper-top{
    width: 100%;
    display: flex;
    border: 1px solid var(--border-clr);
    background: var(--main-clr);
    border-radius: var(--radius) var(--radius) 0 0;
    box-shadow: var(--material-shadow);
}
.outlinks-wrapper-top div{
    width: 17.5%;
    padding: 0.8em;
}
.outlinks-wrapper-top div:first-child{
    width: 30%;
}
.outlinks-wrapper-top div p{
    text-align: left;
    font-size: 0.9rem;
    color: var(--font-light);
}

.outlinks-empty{
    text-align: left;
    padding: 1em;
    color: var(--secondary-clr);
}

/* Available outlinks */
.outlinks{
    width: 100%;
    display: flex;
    flex-direction: column;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border: 1px solid var(--border-clr);
}
.outlink{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.outlink-post{
    background: var(--main-clr);
    width: 100%;
    height: max-content;
    display: flex;
    border-bottom: 1px solid var(--border-clr);
}
.outlink-post-div{
    width: 17.5%;
    padding: 0.8em;
    overflow: hidden;
    height: 100%;
}
.outlink-post-div:first-child{
    width: 30%;
}
.outlink-post-div p{
    color: var(--font-clr);
    text-align: left;
    font-size: 0.9rem;
}
.outlink-post-div a{
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    color: var(--secondary-clr);
    white-space: normal;
    font-size: 0.9rem;
    text-align: left;
}
.outlink-post-owner{
    text-transform: capitalize;
}

/* Outlink actions */
.outlink-actions-wrapper{
    position: relative;
    display: flex;
    width: 16.5%;
    padding: 1em;
}
.outlink-actions-btn{
    position: relative;
    background: transparent;
    border: none;
    cursor: pointer;
    z-index: 1;
}
.outlink-actions{
    position: absolute;
    background: var(--secondary-clr);
    border-radius: var(--radius);
    bottom: -50px;
    left: -4px;
    width: max-content;
    height: max-content;
    display: flex;
    flex-direction: column;
    z-index: 2;
    overflow: hidden;
}
.outlink-actions button{
    background: var(--secondary-clr);
    color: var(--main-clr);
    border: none;
    border-bottom: 1px solid var(--main-clr);
    padding: 0.5em 1em;
    cursor: pointer;
    transition: background 0.3s;
}
.outlink-actions button:hover{
    background: var(--intense-clr);
}
